@import "../../../assets/styles/mixin.scss";
@import "../../../assets/styles/variable.scss";
.com-bestcase {
  background-color: black;
  padding-bottom: 3rem;
  color: white;
  font-family: "Montserrat", sans-serif;
  overflow: hidden;
  .block-reward {
    position: absolute;
    top: -50px;
    right: 60px;
    width: 100px;
    height: 100px;
    background-color: white;
    border-radius: 50%;
    margin: auto;
  }
  .slider-track {
    transition: all 1s cubic-bezier(0.7, 0, 0.3, 1);
  }
  .slick-slide {
    border-radius: 22px;
  }
  .item,
  .wrapper-img {
    cursor: url("../../../assets/images/cursor/slider_icon_2.png"), auto;
  }
  & > .title {
    font-size: 2rem;
    max-width: 320px;
    margin: $pdMobile;
    @include title();
    @include textGlitch("titleBestCase", 17, white, rgba(0, 0, 0, 0.041), red, blue, 400, 100);
  }
  .block-game {
    .wrapper-reward {
      .wrapper-img {
        position: relative;
        // min-height: 230px;
        overflow: hidden;
        border-top-left-radius: unset;
        border-bottom-left-radius: unset;
        .img-bg {
          width: calc(100% - 4rem);
          object-fit: cover;
          margin: auto;
          max-width: 700px;
          border-radius: 22px;
          transition: all 1s cubic-bezier(0.7, 0, 0.3, 1);
          @include desktop2k{
            max-width: 35vw;
          }
        }
      }
      .block-name {
        position: relative;
        height: 50px;
        @include desktop2k{
          height: 100px;
        }
        .img-reward {
          width: 75%;
          position: absolute;
          bottom: 0;
          left: 0;
          right: 0;
          top: 0;
          margin: auto;
        }
      }
    }
    .block-item {
      .item {
        position: relative;
        line-height: 1.5;
        padding: 0 $pdMobile;
        // padding-top: 0;
        text-align: center;
        .name {
          font-weight: 800;
          font-size: 2rem;
          @include desktop2k{
            font-size: 3.5rem;
          }
        }
        .title {
          font-size: 1rem;
          margin-bottom: 2rem;
        }
        .content {
          font-weight: 500;
          font-style: italic;
          font-size: 1rem;
          &.double {
            padding: 0 1.5rem;
            padding-top: 1rem;

          }
        }
        img.double {
          position: absolute;
          transform:  scale(0.8);
        }
        .last-double {
          position: absolute;
          right: 2rem;
          transform: rotate(180deg) scale(0.8);
        }
        .person {
          font-weight: 800;
          font-size: 10px;
          text-transform: uppercase;
          color: $hong-color;
          // margin-top: 1rem;
          padding: 1rem;
        }
      }
    }
    .btn-bestcase-desk {
      display: none;
    }
  }
  .btn-bestcase {
    margin: 2rem $pdMobile;
    margin-top: 3rem;
    @include btn();
  }
}

@include tablet {
  .com-bestcase {
    .block-game {
      .wrapper-reward {
        .wrapper-img {
          min-height: 500px;
        }
      }
    }
  }
}

@include desktop {
  .com-bestcase {
    .block-reward {
      right: 80px;
      @include desktop2k{
        width: 200px;
        height: 200px;
        top: -100px;
      }
    }
    .slick-slider {
      max-width: 44vw !important;
    }
    .slick-dots {
      bottom: unset;
    }
    & > .title {
      font-size: 3rem;
      padding: unset;
      margin: 3.5rem auto;
      max-width: fit-content;
      @include textGlitch("titleBestCase", 17, white, rgba(0, 0, 0, 0.041), red, blue, 800, 100);
      @include desktop2k{
        font-size: 5rem;
        margin: 5rem auto;
      }
    }
    .block-game {
      display: flex;
      justify-content: space-between;
      padding-bottom: 50px;
      .wrapper-reward {
        min-width: 50vw;
        .wrapper-img {
          min-height: 500px;
          @include desktop2k{
            min-height: 900px;
          }
          .img-bg {
            min-height: 500px;
            border-radius: 50px;
            position: absolute;
            right: 2rem;
            @include desktop2k{
              min-height: 900px;
            }
          }
        }
        .block-name {
          position: relative;
          display: flex;

          .name {
            max-width: 25vw;
            font-weight: 800;
            font-size: 2.25rem;
            margin-left: $pdDesk;
            padding-left: 0;

          }
        }
      }
      .block-item {
        min-height: 550px;
        .item {
          padding: 4vw;
          .name {
            text-align: center;
            text-transform: uppercase;
          }
          .title {
            text-align: center;
            margin-bottom: 3rem;
            text-transform: uppercase;
            @include desktop2k{
              font-size: 2rem;
            }
          }
          .content {
            font-size: 25px;
            line-height: 30px;
            @include desktop2k{
              font-size: 1.75rem;
              line-height: 1.5;
            }
          }
          .last-double {
            right: 4vw;
          }
          .person {
            font-size: 1rem;
            margin-top: 3rem;
            color: white;
          }
        }
      }
      .btn-bestcase-desk {
        display: inline-block;
        @include btnDesk();
        @include btn-effect($hong-color);
      }
    }
    .btn-bestcase {
      display: none;
    }
  }
  .wrapper-right {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
